.singlePost{
width: 75%;
padding-left:10px ;
margin-top: 20px;

}

.singlePostWrapper{
padding: 20px;
padding-right: 0;
display: flex;
flex-direction: column;

}

.singlePostImg{
    width: 100%;
    height: 400px;
    border-radius: 5px;
    object-fit: cover;
}
.singlePostTitle{
    text-align: center;
    margin:10px;
    font-family: 'Lora',serif;
    font-size: 28px;
}
.singlePostEdit{
    float: right;
    font-size: 18px;

}
.singlePostIcon{
    margin-left: 10px;
    cursor: pointer;
   
}
.singlePostIcon:first-child{
    color: teal;
}
.singlePostIcon:last-child{
    color: tomato;
}
.singlePostInfo{
    margin-bottom: 20px;
    display: flex;
    justify-content: space-between;
    font-size: 16px;
    font-family: "Varela Round", Arial, Helvetica, sans-serif;
    color: #b39656;
}
.singlePostDesc{
    color:#666;
    font-size: 18px;
    line-height: 25px;
    
}
.singlePostDesc::first-letter{
    margin-left:20px;
    font-size:30px;
    font-weight: 600;
}

.singlePostDescInput {
    border: none;
    color: #666;
    font-size: 18px;
    line-height: 25px;
    width: 100%;
    min-height:150px;
    font-family: "Varela Round", Arial, Helvetica, sans-serif;
  }
  
  .singlePostDescInput:focus {
    outline: none;
   
  }
  .singlePostButton {
    width: 100px;
    border: none;
    background-color: teal;
    padding: 5px;
    color: white;
    border-radius: 5px;
    cursor: pointer;
    align-self: flex-end;
    margin-top: 20px;
   
  }
  
.singlePostTitleInput {
    margin: 10px;
    font-family: "Varela Round", Arial, Helvetica, sans-serif;
    font-size: 28px;
    text-align: center;
    border: none;
    color: gray;
    border-bottom: 1px solid lightgray;
    outline: none;
  }
  
  .singlePostTitleInput:focus {
    outline: none;
    
  }
 